<div class="home ms-auto me-auto"><form class="form text-capitalize" @submit.prevent="takeData"><p class="text-center title d-flex justify-content-center position-relative align-items-center">Write Your To do list</p><label><input class="input" required v-model="todo_data.title" placeholder="" type="text"><span>Enter Your work you have to do it </span></label><div class="flex d-flex justify-content-center"><label><span class="date">From</span><input class="input" required v-model="todo_data.from" placeholder="" type="date"></label><label><span class="date">To</span><input class="input" required v-model="todo_data.to" placeholder="" type="date" :min="todo_data.from"></label></div><label class="check-complete d-flex align-items-center justify-content-center"><input ref="check" type="checkbox" @change="todo_data.isCompleted = true"><svg viewBox="0 0 64 64" height="1em" width="1em"><path class="path" d="M 0 16 V 56 A 8 8 90 0 0 8 64 H 56 A 8 8 90 0 0 64 56 V 8 A 8 8 90 0 0 56 0 H 8 A 8 8 90 0 0 0 8 V 16 L 32 48 L 64 16 V 8 A 8 8 90 0 0 56 0 H 8 A 8 8 90 0 0 0 8 V 56 A 8 8 90 0 0 8 64 H 56 A 8 8 90 0 0 64 56 V 16" pathLength="575.0541381835938"></path></svg><span class="d-block ms-1">completed</span></label><button class="submit">Enter</button></form></div>